const listGallery = document.querySelector('.gallery');

//перебор объекта для получения параметров и создания массива->объекта simplelightbox
 const createItemsGallery = images.map(el => fnCreateCardItem(el)).join('');

//созд карточки
function fnCreateCardItem(param) {
return `<li class="gallery-item">
  <a class="gallery-link" href="${param.original}">
    <img
      class="gallery-image"
      src="${param.preview}"
      alt="${param.description}"
    />
  </a>
</li>`
};

